/* Exo2 Regular */
@font-face {
	font-family: 'Exo2';
	src: url('../fonts/Exo2/Exo2-Light.eot');
	src: local('Exo2 Light'), local('Exo2-Light'),
		url('../fonts/Exo2/Exo2-Light.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Exo2/Exo2-Light.woff') format('woff'),
		url('../fonts/Exo2/Exo2-Light.woff2') format('woff2'),
		url('../fonts/Exo2/Exo2-Light.ttf') format('truetype');
	font-weight: 300;
	font-style: normal;
	font-display: swap;
}
/* Exo2 Bold */
@font-face {
	font-family: 'Exo2';
	src: url('../fonts/Exo2/Exo2-Bold.eot');
	src: local('Exo2 Bold'), local('Exo2-Bold'),
		url('../fonts/Exo2/Exo2-Bold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Exo2/Exo2-Bold.woff') format('woff'),
		url('../fonts/Exo2/Exo2-Bold.woff2') format('woff2'),
		url('../fonts/Exo2/Exo2-Bold.ttf') format('truetype');
	font-weight: 700;
	font-style: bold;
	font-display: swap;
}